import React, { useState } from 'react';
import Button from '../../../components/ui/Button';
import InputText from '../../../components/ui/InputText';
import iMeasurementDataTwo from '../../../interface/measurement-two-data.inteface';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onSave: (data: iMeasurementDataTwo) => void,
    data?: iMeasurementDataTwo
}

const MeasureStepTwo : React.FC<Props>  = ({
    onSave,
    data
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('measures-step');
    const [localData, setLocalData] = useState<iMeasurementDataTwo>(data || {
        left: {
            height: '',
            pd: ''
        },
        right: {
            height: '',
            pd: ''
        }
    })

    // HOOKS

    // METHODS
    const onSaveHandler = () => {
        onSave(localData);
    }

    const inputChangeHandler = (e: any, position: 'left' | 'right', key: 'pd' | 'height') => {

        setLocalData((prev: any) => {
            return {
                ...prev,
                [position]: {
                    ...prev[position],
                    [key]: e.target.value
                }
            }
        })

    }

    return (
        <div className={B()}>

            <div className={E('form', 'two')}>
                <label>Left eye</label>
                <label>Right eye</label>

                <label>PD</label>
                <label>PD</label>

                <InputText type="light" value={localData?.left.pd} onChange={(v) => inputChangeHandler(v, 'left', 'pd')}/>
                <InputText type="light" value={localData?.right.pd} onChange={(v) => inputChangeHandler(v, 'right', 'pd')}/>

                <label>Height</label>
                <label>Height</label>

                <InputText type="light" value={localData?.left.height} onChange={(v) => inputChangeHandler(v, 'left', 'height')}/>
                <InputText type="light" value={localData?.right.height} onChange={(v) => inputChangeHandler(v, 'right', 'height')}/>

            </div>

            <Button text="Next" icon="right-arrow-circle-white" className={E('button')} click={onSaveHandler}/>

        </div>
    )
}

export default MeasureStepTwo;