import React from 'react';
import { useDispatch } from 'react-redux';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import iMeasurementDataOne from '../../../interface/measurement-one-data.inteface';
import iMeasurementDataThree from '../../../interface/measurement-three-data.inteface';
import iMeasurementDataTwo from '../../../interface/measurement-two-data.inteface';
import measureSlice from '../../../store/slices/measure.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    // TEMP
    empty?:boolean,

    one?: iMeasurementDataOne,
    two?: iMeasurementDataTwo,
    three?: iMeasurementDataThree,
    index: number
}

const MeasureDataCard : React.FC<Props>  = ({
    empty = false,
    one,
    two,
    three,
    index
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('measure-data-card');
    const dispatch = useDispatch();

    // HOOKS

    // METHODS
    const onEdit = () => {
        dispatch(measureSlice.actions.changeView('measure'));
        dispatch(measureSlice.actions.changeStep(0));
        dispatch(measureSlice.actions.editPair(index));
    }

    const onAdd = () => {
        dispatch(measureSlice.actions.changeView('measure'));
        dispatch(measureSlice.actions.changeStep(0));
        dispatch(measureSlice.actions.addPair());
    }

    return (
        <div className={B( empty ? 'empty' : '' )}>
            <div className={E('head')}>
                <label>Pair {index + 1}</label>
                <span>{one?.price} </span>
            </div>

            <div className={E('content')}>
                    <div className={E('data', 'one')}>      
                        <label className={E('data-group')}>Measure 1</label>    
                        <div className={E('data-field')}>                                                                              
                            <label>A B</label>
                            <span>{(one?.a || 0) + (one?.b || 0)} mm</span>                                

                            <label>D</label>
                            <span>{one?.d}mm</span> 
                        </div>                               
                    </div>

                    <div className={E('data', 'two')}>                                
                        <label className={E('data-group')}>Measure 2</label>       

                        <div className={E('data-field')}>
                            <label>Left Eye PD</label>
                            <span>{two?.left.pd}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Left Eye Height</label>
                            <span>{two?.left.pd}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Right Eye PD</label>
                            <span>{two?.right.pd}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Right Eye Height</label>
                            <span>{two?.right.pd}</span>                                
                        </div>
                    </div>

                    <div className={E('data', 'three')}>                                
                        <label className={E('data-group')}>Measure 3</label>       

                        <div className={E('data-field')}>
                            <label>Pantoscopic Angle</label>
                            <span>{three?.pantoscopicAngle}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Wrap Angle</label>
                            <span>{three?.wrapAngle}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Vertex distance</label>
                            <span>{three?.vertexDistance}</span>                                
                        </div>
                        <div className={E('data-field')}>
                            <label>Reading distance</label>
                            <span>{three?.readingDistance}</span>                                
                        </div>
                    </div>
            </div>{/* CONTENT END*/}
            

            <div className={E('footer')} onClick={onEdit}>
                Edit <Icon name="pencil-white"/>
            </div>

            { 
                empty && 
                <Button 
                    click={onAdd}
                    icon="plus" 
                    type="secondary" 
                     
                    circle={true}
                /> 
            }
        </div>
    )
}

export default MeasureDataCard;