
import { isConstructorDeclaration } from 'typescript';
import kidsProductLogic from '../constants/product-logic-kids.constants';
import { TEMPORARY_PRODUCT_LIST } from '../constants/products.constants';
import { AddMatrixValues, fetchBrandDetailsViaId, fetchProductDetailsViaId, getAnswerIds } from './misc';

const product_ids = [`stellest`,`eyezen_kids`,`crizal_easy`,`crizal_prevencia`]
const brand_ids = [`stellest`,`eyezen`,`crizal`,`crizal`]

export const evaluateKidsDecisionTree = (
    currentNeedExplorerData: any
) => {
    console.log(currentNeedExplorerData)

    //(1) get all answers from children to parents ANSWER_IDS
    const allAnswers = [
        ...currentNeedExplorerData.questions[0].questions,
        ...currentNeedExplorerData.questions[1].questions
    ]

    const ANSWER_IDS = getAnswerIds(allAnswers)
    console.log('ANSWER IDS: ', ANSWER_IDS)

    //(2) get ALL_PRODUCT_LOGIC_MATRIX for general and extra
    const ALL_PRODUCT_LOGIC_MATRIX = kidsProductLogic
    console.log('ALL PRODUCT: ', ALL_PRODUCT_LOGIC_MATRIX)

    //(3) filter the ALL_PRODUCT_LOGIC_MATRIX with ANSWER_IDS
    let ANSWER_MATRIX = []
    for (var i = 0; i < ANSWER_IDS.length; i++) {
        var item  = ALL_PRODUCT_LOGIC_MATRIX.find(m => m.answerId === ANSWER_IDS[i]); 
        ANSWER_MATRIX.push(item?.scoreMatrix)
    }
    console.log('ANSWER MATRIX: ', ANSWER_MATRIX)

    //(4) Add ANSWER_MATRIX
    const MATRIX_SUM = AddMatrixValues(ANSWER_MATRIX)
    console.log('SUM: ',MATRIX_SUM)

    //(5) Apply result to TEMP
    let index_correct = {
        pair1: MATRIX_SUM[1] > MATRIX_SUM[0] ? 1 : 0,
        pair2: MATRIX_SUM[1] > MATRIX_SUM[0] ? 0 : 1
    }

    let index_enhance = {
        pair1: MATRIX_SUM[3] > MATRIX_SUM[2] ? 3 : 2,
        pair2: MATRIX_SUM[3] > MATRIX_SUM[2] ? 2 : 3
    }

    const TEMP = {
        pair1: {
            correct: {
                brand: fetchBrandDetailsViaId(brand_ids[index_correct.pair1]),
                product: fetchProductDetailsViaId(product_ids[index_correct.pair1])
            },
            // If correct is eyezen kids; index = 1
            // then EPS as 1st pair	
            protect: [{
                brand: index_correct.pair1 === 1 ? fetchBrandDetailsViaId('blueuv_filter_system') : {},
                product: index_correct.pair1 === 1 ? fetchProductDetailsViaId('EyeProtectSystem') : {}
            }],
            enhance: {
                brand: fetchBrandDetailsViaId(brand_ids[index_enhance.pair1]),
                product: fetchProductDetailsViaId(product_ids[index_enhance.pair1])
            }
        },
        pair2: {
            correct: {
                brand: fetchBrandDetailsViaId(brand_ids[index_correct.pair2]),
                product: fetchProductDetailsViaId(product_ids[index_correct.pair2])
            },
            // If correct is eyezen kids; index = 1
            // then Transitions Gen 8 as 2nd pair
            protect: [{
                brand: index_correct.pair1 === 1 ? fetchBrandDetailsViaId('transitions') : {},
                product: index_correct.pair1 === 1 ? fetchProductDetailsViaId('transitions_signature_gen_8') : {}
            }],
            enhance: {
                brand: fetchBrandDetailsViaId(brand_ids[index_enhance.pair2]),
                product: fetchProductDetailsViaId(product_ids[index_enhance.pair2])
            }
        }
    }

    return TEMP
}