import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Modal from '../../../components/Modal';
import Icon from '../../../components/ui/Icon';
import anamnesisDetails from '../../../constants/anamnesis-summery.constants';
import { TEMPORARY_PRODUCT_LIST } from '../../../constants/products.constants';
import { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import { getNeedExplorer } from '../../../store/slices/need-explorer.slice';
import useBEM from '../../../utils/hooks/useBEM';
import { fetchProductDetailsViaId } from '../../../utils/misc';

interface Props {
    onClose: () => void
}

const NeedExplorerAnamnesisDetails : React.FC<Props>  = ({
    onClose
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('need-explorer-anamnesis-details');
    const [details, setDetails] = useState(anamnesisDetails)
    const { selectedId } = useSelector(getNeedExplorer)

    // HOOKS
    useEffect(() => {
        setDetails({
            ...details,
            side: {
                tag: selectedId?.label,
                title: selectedId?.smallTitle,
                description: selectedId?.description,
            },
            summary: details.summary
        })
    },[])

    return (            
        <Modal show={true} radius="8px" width="85rem" height="55rem" transparent={true} hideCloseButton={true}>
                <div className={B()}>
                    <div className={E('side')}>                        
                        <span className={E('side-tag')}>
                            {details.side.tag}
                        </span>                        
                        <h5>
                            {details.side.title}
                        </h5>
                        <p className={E('side-description')}>
                            {details.side.description}
                        </p>
                    </div>

                    <div className={E('details')}>
                        <div className={E('scroll')}>
                            {
                                details.summary.map( (summary, index) => (
                                    <div className={E('summary')} key={index}>
                                        <h5>
                                            {summary.question}
                                        </h5>
                                        {
                                            summary.answers.map((answer, i) => (
                                                <span className={E('summary-answer')} key={i}>
                                                    {answer}
                                                </span>
                                            ))
                                        }                                   
                                    </div> 
                                ))
                            }
                        </div>
                    </div>
                </div>

                <Icon name="close-gray" className={E('close')} click={onClose}/>
        </Modal>
    )
}

export default NeedExplorerAnamnesisDetails;