import React, { useEffect, useState } from 'react';
import { BRANDS } from '../../../constants/products.constants';
import useBEM from '../../../utils/hooks/useBEM';
import ProductCard from './ProductCard';

interface Props {

}

const ProductSelection : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('product-selection');
    const brands = BRANDS;
    const listDynamicWidth = `${(brands.length * 30)}rem`;
    const [fadeIn, setFadeIn] = useState(false);

    // HOOKS
    useEffect(() => {
        setTimeout(() => {
            setFadeIn(true);
        }, 250);
    }, [])

    // METHODS

    return (
        <div className={B( fadeIn ? 'fade-in' : '')}>
            <div className={E('scroll')}>
                <ul className={E('list')} style={{width: listDynamicWidth}}>
                    { brands.map( (b: any) => (                                    
                        <ProductCard 
                            key={b.id}
                            id={b.id}
                            label={b.label}
                            background={b.background}
                            logo={b.logo}
                            subProducts={b.products}
                        />                                    
                    ))}
                </ul>
            </div>
        </div>
    )
}

export default ProductSelection;