import React, { useEffect, useState } from 'react';
import HalfCircleProgress from '../../../components/ui/HalfCircleProgress';
import Icon from '../../../components/ui/Icon';
import useBEM from '../../../utils/hooks/useBEM';
import Slider from "react-input-slider";
import Button from '../../../components/ui/Button';
import { useDispatch, useSelector } from 'react-redux';
import lensThicknessSlice, { getlensThickness } from '../../../store/slices/lens-thickness.slice';
import { LENS_THICKNESS_RULES } from '../../../constants/lens-thickness.constants';
import { getUser } from '../../../store/slices/user.slice';
import lensConsultationSlice, { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import LensPairType from '../../../types/lens-pair.type';
import iSubProduct from '../../../interface/sub-product.inteface';
import LensFamilyType from '../../../types/lens-family.type';
import useLensConsultation from '../../lens-consultation/useLensConsultation';
import { getApp } from '../../../store/slices/app.slice';


interface Props {

}

const LensThicknessControl : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('lens-thickness-control');
    const [slider, setSlider] = useState(0);
    const dispatch = useDispatch();

    const { getSubProductsByLabel } = useLensConsultation();
    const { prescription } = useSelector(getUser);
    const { lensThicknessSettings } = useSelector(getApp);
    const { tempLensThickness } = useSelector(getlensThickness);
    const { activePair, lensRecommendation, sliderPage } = useSelector(getLensConsultation);
    const [ subProducts, setSubProducts ] = useState<iSubProduct[]>([]);

    useEffect(() => {
        if(sliderPage < 3)return;

        const subProductsResult = getSubProducts(activePair);
        setSubProducts(filterSubProductsWithEnabledIndex(subProductsResult));
        
        
        if(tempLensThickness && tempLensThickness[activePair] && 
            lensRecommendation && lensRecommendation[activePair]){
            recommendLensThickness(subProductsResult);
        } else {
            setSliderValuePairLoad(subProductsResult);
        }
        
    }, [activePair, sliderPage])

    // METHODS
    const setSliderValuePairLoad = (subp: iSubProduct[]) => {
        if(!lensRecommendation)return;
        let sliderIndex = 0;
        subp.forEach( (x,i) => {
            if(lensRecommendation[activePair]?.correct.subProduct?.index === x.index){
                sliderIndex = i;
            }
        });        
        setSlider(sliderIndex);
    }

    const recommendLensThickness = (subProducts: iSubProduct[]) => {
        const dontProcess = !tempLensThickness || !tempLensThickness[activePair];
        if(dontProcess)return;
        const activeLensThickness = (tempLensThickness as any)[activePair]
        const filteredSubProducts = filterSubProductsWithEnabledIndex(subProducts);
        let recommended = filteredSubProducts.filter( x => x.index === activeLensThickness);
        if(recommended.length === 0) {
            recommended = getTheClosestSubProductByIndex(filteredSubProducts, activeLensThickness);
        } 
        if(recommended.length === 0) return;
        
        setFinalSubProduct(recommended[0]);
        dispatch(lensThicknessSlice.actions.setLensThicknessOnePair({
            value: null,
            pair: activePair
        }));
        setSlider(filteredSubProducts.map(x => x.index).indexOf(recommended[0].index));
    }

    const getTheClosestSubProductByIndex = (subProducts: iSubProduct[], index: number) => {
        const indexes = subProducts.map( x => x.index);        
        const closest = indexes.reduce(function(prev, curr) {
            return (Math.abs(curr - index) < Math.abs(prev - index) ? curr : prev);
        }, 0);
        return subProducts.filter( x => x.index === closest);
    }

    const recommendedSubProduct = (): iSubProduct | any => {
        if(!lensRecommendation || !lensRecommendation[activePair])return null;
        return lensRecommendation[activePair]?.correct?.subProduct as iSubProduct;
    }

    const getSubProducts = (pair: LensPairType): iSubProduct[] => {
        if(!lensRecommendation)return [];

        const correct = lensRecommendation[pair]?.correct;
        const label = correct?.subProduct?.label || '';
        if(!correct?.product)return [];

        return getSubProductsByLabel(
                label, 
                correct.product.subProducts as iSubProduct[], 
                true
            );
    }

    const viewPrescription = () => {
        dispatch(lensThicknessSlice.actions.changeView('prescription'));
    }
    
    let timerThrottle: any = null;
    const setManualThickness = (value: number) => {
        if(timerThrottle)clearTimeout(timerThrottle);
        timerThrottle = setTimeout(() => {
            setFinalSubProduct(subProducts[value])
        },300);
        setSlider(value);
    }

    const setFinalSubProduct = (subProduct: iSubProduct) => {
        if(!lensRecommendation)return;
        const correct = lensRecommendation[activePair]?.correct;
        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.CORRECT,
            brand: correct?.brand,
            product: correct?.product,
            subProduct,
            manual: true,
            pair: activePair
        }));
    }

    const updateThickness = (action: string) => {
        if( slider === 0 && action === 'minus')return;
        if( slider === subProducts.length - 1 && action === 'add' )return;

        const newIndex = action === 'add' ? (slider + 1) : (slider - 1);
        setManualThickness(newIndex);
    }

    const isPrescriptionComplete = () => {
        return prescription.values.sphere.od ? true : false
    }

    const filterSubProductsWithEnabledIndex = (subProducts: iSubProduct[]) => {
        const allIndex = lensThicknessSettings.filter(x=>x.enabled).map( x => x.value);
        return subProducts.filter( x => allIndex.includes(x.index));
    }

    return (
        <div className={B()}>
            
            <div  onClick={viewPrescription} className={E('prescription', isPrescriptionComplete() ? 'completed' : '')}>
                Prescription                
                <span>Completed</span>                
                <Button icon={isPrescriptionComplete() ? 'pencil' : 'plus'}  
                    type="secondary" circle/>
            </div>

            <div className={E('controls')}>
                
                <div className={E('controls-head')}>
                    <label>Lens Thickness</label> 
                    <span>{recommendedSubProduct() && recommendedSubProduct().index}</span>
                </div>

                <div className={E('controls-half-circles')}>                    
                    <HalfCircleProgress value={-50} label="Weight" />
                    <HalfCircleProgress value={-5} label="Center Thickness*" />
                    <HalfCircleProgress value={2} label="Edges Thickness*" />
                </div>

                <div className={E('controls-slider')}>
                    <Icon name="minus-circle-small" click={() => updateThickness('minus')}/>
                    <Slider
                            styles={{
                                active: {
                                backgroundColor: "#0386D6",
                                },
                            }}
                            axis="x"
                            xstep={1}
                            xmin={0}
                            xmax={subProducts.length - 1}
                            x={slider}
                            onChange={({ x }) => setManualThickness(x)}
                    />
                    <Icon name="plus-circle-small" click={() => updateThickness('add')}/>
                    <label className={E('controls-slider-index')}>
                        {new Array(subProducts.length).fill('').map((item, index) => (
                            <span key={index}></span>
                        ))}
                    </label>
                </div>


                <label className={E('controls-instruction')}>*Compared to the reference index</label>
                <Button text="View more details" icon="right-arrow-circle-white" soon/>
            </div>

        </div>
    )
}

export default LensThicknessControl;
