import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import RouterLink from '../../../components/ui/RouterLink';
import anamnesis from '../../../constants/anamnesis.constants';
import NeedExplorerViewType from '../../../types/need-explorer-view.type';
import needExplorerSlice, { getNeedExplorer } from '../../../store/slices/need-explorer.slice';
import useBEM from '../../../utils/hooks/useBEM';
import NeedExplorerAnamnesisDetails from './NeedExplorerAnamnesisDetails';
import { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import { fetchProductDetailsViaId } from '../../../utils/misc';
interface Props {

}

const NeedExplorerAnamnesis : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('need-explorer-anamnesis');
    const dispatch = useDispatch();
    const [showSummary, setShowSummary] = useState<boolean>(false);
    const [result, setResult] = useState<{ tag: string | undefined, text: string | undefined }[]>(anamnesis);
    const currentLensConsultation = useSelector(getLensConsultation);

    // HOOKS
    useEffect(()=>{
        const { lensRecommendation } = currentLensConsultation 
        
        const result = [
            {
                tag: lensRecommendation?.pair1?.correct.product?.label,
                text: 'Recommended design to correct your vision for pair 1'
            },
            {
                tag: lensRecommendation?.pair2?.correct.product?.label,
                text: 'Recommended design to correct your vision for pair 2'
            },
            {
                tag: lensRecommendation?.pair1?.protect[0].product?.label,
                text: 'Recommended technology to protect your eyes for pair 1'
            },
            {
                tag: lensRecommendation?.pair2?.protect[0].product?.label,
                text: 'Recommended technology to protect your eyes for pair 2'
            },
            {
                tag: lensRecommendation?.pair1?.enhance.product?.label,
                text: 'Recommended coating to enhance clarity for pair 1'
            },
            {
                tag: lensRecommendation?.pair2?.enhance.product?.label,
                text: 'Recommended coating to enhance clarity for pair 2'
            },
        ]

        setResult(result)
    },[])

    // METHODS
    const modify = () => {        
        dispatch(needExplorerSlice.actions.questionaireFromStart());
        dispatch(needExplorerSlice.actions.changeView(NeedExplorerViewType.QUESTIONNAIRE));
    }

    const gotoAnamnesis = () => {
        dispatch(needExplorerSlice.actions.changeView(NeedExplorerViewType.ANAMNESIS));
    }

    const onSelectCard = (index: number) => {
        // populate id to need explorer state
        const id = getId(index)

        const product = fetchProductDetailsViaId(id)

        dispatch(needExplorerSlice.actions.populateSelectedModal(product));

        setShowSummary(true);
    }

    const getId = (index: number) => {
        const { lensRecommendation } = currentLensConsultation 

        // index 0 = correct pair 1
        // index 1 = correct pair 2
        // index 2 = protect pair 1
        // index 3 = protect pair 2
        // index 4 = enhance pair 1
        // index 5 = enhance pair 2

        switch(index){
            case 0: return lensRecommendation?.pair1?.correct.product?.id
            case 1: return lensRecommendation?.pair2?.correct.product?.id
            case 2: return lensRecommendation?.pair1?.protect[0].product?.id
            case 3: return lensRecommendation?.pair2?.protect[0].product?.id
            case 4: return lensRecommendation?.pair1?.enhance.product?.id
            case 5: return lensRecommendation?.pair2?.enhance.product?.id
            default: return ''
        }
    }
    

    return (
        <div className={`${B()} page`}>

            <div className={E('left')}>                    
                <Icon name="user"/>                
                <Button 
                    className={E('improve')}
                    text="Improve my anamnesis" 
                    icon="right-arrow-circle-white" 
                    click={gotoAnamnesis}/>

                <RouterLink className={E('modify')} click={modify}>
                    Modify my answers
                </RouterLink>
            </div>


            <div className={E('cards')}>
                {
                    result.map((d, index) => (
                        d.tag ? <div className={E('card')} key={index} onClick={() => onSelectCard(index)}>
                            <span className={E('card-tag')}>
                                {d.tag}
                            </span> 
                            <h5>
                                {d.text}
                            </h5> 

                            <Icon name="right-arrow-circle" className={E('card-icon')}/>
                        </div> : null
                    ))
                }                   
            </div>

            {
                showSummary && 
                <NeedExplorerAnamnesisDetails onClose={() => setShowSummary(false)}/>
            }
            

        </div>
    )
}

export default NeedExplorerAnamnesis;