import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import iLensColor from '../../../interface/lens-color.inteface';
import { getLensConsultation } from '../../../store/slices/lens-consultation.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    colors: iLensColor[],
    onSelect?: (color: iLensColor) => void,
    selected?: iLensColor
}

const ProtectColorPicker : React.FC<Props>  = ({
    colors,
    onSelect,
    selected
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('protect-color-picker');
    const [showColors, setShowColors] = useState(false);
    const { lensRecommendation, activePair, productFamily } = useSelector(getLensConsultation);
    const [selectedColor, setSelectedColor] = useState<string>();

    // HOOKS
    useEffect(() => {
        const protect = getProtectLensImage();
        setSelectedColor( protect && protect.color ? protect.color.label : '' );
    }, [lensRecommendation, activePair]);

    // METHODS
    const inlineStyle = (color: string) => {
        return {
            backgroundColor: color,
            color: color
        }
    }

    const toggleColors = () => {
        setShowColors(prev => !prev);
    }

    const selectColor = (color: iLensColor) => {
        if(onSelect)onSelect(color);
    }

    const optionClassModifier = () => {
        const classes = ['toggle'];
        if(showColors)classes.push('toggle-closed');
        if(selectedColor)classes.push('with-selected-color');

        return classes;
    }

    const getProtectLensImage = () => {
        if(!lensRecommendation)return '';
        const protect = lensRecommendation[activePair]?.protect;
        if(!protect || protect.length === 0)return '';
        return protect[0];
    }

    return (
        <div className={B()}>
            <ul className={E('options')}>
                <li 
                    onClick={toggleColors}
                    className={E('option', optionClassModifier())}>
                        <span>
                            {selectedColor ? selectedColor : 'Please select color'}
                        </span>
                    </li>
                {
                    showColors && colors.map( (c:iLensColor) => (
                        <li 
                            onClick={() => selectColor(c)}
                            className={E('option', (selected && selected.id === c.id) ? 'selected' : '')} 
                            key={c.id} style={inlineStyle(c.value)}></li>
                    ))
                }
            </ul>
        </div>
    )
}

export default ProtectColorPicker;