import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import packages from '../../constants/package-offer.constants';
import iPackageOfferStore from '../interface/package-offer-store-interface';
import iRootState from '../interface/root-state.interface';

const initialState: iPackageOfferStore  = {
    data: packages,
}

const packageOfferSlice = createSlice({
    name: 'packageOffer',
    initialState,
    reducers: {
        setPackageOfferData: (state, action: PayloadAction<any>) => {
            const { lr, ap} = action.payload
            const { pair1, pair2 } = lr

            const p1d = pair1.correct.product.downgrade
            const p1 =  pair1.correct.subProduct ? pair1.correct.subProduct.label : ''

            const p2d = pair2.correct.product.downgrade
            const p2 = pair2.correct.subProduct ? pair2.correct.subProduct.label : ''

            const cat = [
                { value: '', label: ap === 'pair1' ? p1 : p2},
                { value: '', label: ap === 'pair1' ? pair1.protect[0].product.label : pair2.protect[0].product.label},
                { value: '', label: ap === 'pair1' ? pair1.enhance.product.label : pair2.enhance.product.label}
            ]

            const downgrade = ap === 'pair1' ? p1d : p2d


            const newData = []

            // For Personalized (index=0), For Optimized (index=1), For Standart (index=2)

            for (var i = 0; i < 3; i++) {
                let temp = {
                    ...packages[i],
                    product: {
                        ...packages[i].product,
                        name: downgrade[i]
                    },
                    categories: cat
                }

                newData.push(temp)
            }
            
            return {
                ...state,
                data: newData
            }
        },
    }
});

export const getPackageOffer = (state:iRootState) => state.packageOffer;
export default packageOfferSlice;