import iTheme from "../../interface/theme.interface";

type utilsType = {
    cloneObject: <T extends unknown>(obj: T) => T,
    downloadAsset: (src: string, progressCallback?: (percent: number) => void) => Promise<string>,
    generateRandomNumbers: (count: number) => number[],
    getThemeVariables: (theme: iTheme) => React.CSSProperties
}

const useUtils  = (): utilsType =>  {

        const cloneObject = <T extends unknown>(obj: T): T => {
            const str = JSON.stringify(obj);
            const json = JSON.parse(str);
            return json;
        }

        const downloadAsset = (src: string, progressCallback?: (percent: number) => void): Promise<string> => {
            const req = new XMLHttpRequest();
            req.open('GET', src, true);
            req.responseType = 'blob';

            req.addEventListener("progress", function (evt) {
                if(evt.lengthComputable) {
                    const percentComplete = ((evt.loaded / evt.total) * 100).toFixed(0);
                    if(progressCallback)progressCallback(Number(percentComplete));
                }
            }, false);

            const promise = new Promise<string>((resolved, reject) => {
                req.onload = function() {                    
                    if (this.status === 200) {
                       const response = this.response;
                       const blobData = URL.createObjectURL(response); // IE10+
                       resolved(blobData);
                    }
                 }
                 req.onerror = (e) => reject(e);
            });
             
            req.send();
            return promise;
        }

        const generateRandomNumbers = (count: number): number[] => {
            const numbers = new Array(count).fill('').map((o,i) => i).sort(() => .5 - Math.random());
            return numbers;
        }

        const getThemeVariables = (theme: iTheme) => {
            return { 
                "--app-theme-main": theme.main,
                "--app-theme-dark": theme.dark,
                "--app-bubble-main": theme?.bubbleMain,
                "--app-bubble-sub": theme?.bubbleSub,      
            } as React.CSSProperties
        }

        return {
            cloneObject,
            downloadAsset,
            generateRandomNumbers,
            getThemeVariables
        }
}

export default useUtils;