import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import Modal from '../../../components/Modal';
import Button from '../../../components/ui/Button';
import Icon from '../../../components/ui/Icon';
import SideBoxBordered from '../../../components/ui/SideBoxBordered';
import { LENS_THICKNESS_RULES } from '../../../constants/lens-thickness.constants';
import routes from '../../../constants/routes.constant';
import lensThicknessSlice, { getlensThickness } from '../../../store/slices/lens-thickness.slice';
import lensConsultationSlice from '../../../store/slices/lens-consultation.slice';
import prescriptionSlice, { getPrescription } from '../../../store/slices/prescription.slice';
import { getProducts } from '../../../store/slices/products.slice';
import userSlice, { getUser } from '../../../store/slices/user.slice';
import LensCategoryType from '../../../types/lens-category.type';
import LensEnhanceType from '../../../types/lens-enhance.type';
import LensFamilyType from '../../../types/lens-family.type';
import LensProtectType from '../../../types/lens-protect.type';
import PrescriptionViewType from '../../../types/prescription-view.type';
import useBEM from '../../../utils/hooks/useBEM';
import useLensConsultation from '../../lens-consultation/useLensConsultation';
import useLensThickness from '../../lens-thickness/useLensThickness';
import PrecisionType from './../../../types/precision.type';
import LensPairType from '../../../types/lens-pair.type';
import ComplementaryEquipmentType from '../../../types/complementary-equipment.type';
import { getApp } from '../../../store/slices/app.slice';
// import PrescriptionLensOverlayAnimation from './PrescriptionLensOverlayAnimation';
import AgeCategoryType from '../../../types/age-category.type';

import clearLens from '../../../assets/images/prescription/Clear_Lens.png';

interface Props {

}

const PrescriptionSide : React.FC<Props>  = () =>  {
    // VARIABLES
    const [B, E] = useBEM('prescription-side');
    const { temporaryProductList } = useSelector(getProducts);
    const { ageCategory } = useSelector(getUser);

    const { 
        view, 
        isValidPrescription, 
        tempPrescriptionData,
        tempGeneralEquipment,
        tempComplementaryEquipment
    } = useSelector(getPrescription);
    const dispatch = useDispatch();
    const history = useHistory();
    const [showImportModal, setShowImportModal] = useState(false);
    const lensThicknessRules = LENS_THICKNESS_RULES;
    const { getRecommendedLensThickness } = useLensThickness();
    const { 
        getRecommendedProductPair1, 
        getRecommendedProductPair2,
        getRecommendedProductPair1Kids
    } = useLensConsultation();
    // const { assets } = useSelector(getApp);

    // HOOKS

    // METHODS
    const next = () => {
        if(view === 'lens-proposal') {            
            dispatch(userSlice.actions.savePrescriptionData(tempPrescriptionData)); 
            
            const sphere: any = tempPrescriptionData.values.sphere;
            const thickness = getRecommendedLensThickness({
                od: sphere.od,
                os: sphere.os
            }, lensThicknessRules);

            saveRecommendLensThickness(thickness);
            if(ageCategory?.value === AgeCategoryType.KIDS) {
                recommendProductsForKids();
            } else {
                recommendProducts();
            }


            dispatch(userSlice.actions.saveLensEquipment({
                general: tempGeneralEquipment,
                complementary: tempComplementaryEquipment
            })); 

            history.push(routes.lensProposalBuilder.path);
            return;
        }
        dispatch(prescriptionSlice.actions.changeView(PrescriptionViewType.LENSPROPOSAL));
    }

    const getGeneralEquipmentValue = (family: LensFamilyType) => {
        return tempGeneralEquipment
        .filter( o => o.field === family)
        .map( o => o.value?.value)[0];
    }

    const getComplementaryEquipmentValue = () => {
        return tempComplementaryEquipment[0].value?.value as ComplementaryEquipmentType
    }

    const recommendProductsForKids = () => {
        clearRecommendedPair2();
        const sphereValues = tempPrescriptionData.values.sphere;
        const highest = Number(sphereValues?.od) > Number(sphereValues?.os) ? 
                        sphereValues.od : 
                        sphereValues.os;    

        const recommendedCorrectPair1 = getRecommendedProductPair1Kids({
            familyType: LensFamilyType.CORRECT,
            prescription: highest as number,
            products: temporaryProductList
        });

        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.CORRECT,
            product: recommendedCorrectPair1.product,
            subProduct: recommendedCorrectPair1.subProduct,
            pair: LensPairType.PAIR1
        }));
    }

    const recommendProducts = () => {
        // Note: Pair 1
        const correctLensCategory = getGeneralEquipmentValue(LensFamilyType.CORRECT) as LensCategoryType;
        const protectLensCategory = getGeneralEquipmentValue(LensFamilyType.PROTECT) as LensProtectType;
        let enhanceLensCategory = getGeneralEquipmentValue(LensFamilyType.ENHANCE) as LensEnhanceType;
        
        if(!enhanceLensCategory && protectLensCategory === LensProtectType.TINTED){
            enhanceLensCategory = LensEnhanceType.TINTED
        }

        const recommendedCorrect = getRecommendedProductPair1({
            familyType: LensFamilyType.CORRECT, 
            lensCategory: correctLensCategory, 
            products: temporaryProductList
        });
        
        const recommendedProtect = getRecommendedProductPair1({
            familyType: LensFamilyType.PROTECT, 
            lensCategory: protectLensCategory, 
            products: temporaryProductList             
        });
        
        const recommendedEnhance = getRecommendedProductPair1({
            familyType: LensFamilyType.ENHANCE, 
            lensCategory: enhanceLensCategory, 
            products: temporaryProductList             
        });

        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.CORRECT,
            product: recommendedCorrect.product,
            subProduct: recommendedCorrect.subProduct,
            pair: LensPairType.PAIR1
        }));

        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.PROTECT,
            product: recommendedProtect.product,
            brand:recommendedProtect.brand,
            manual: false,
            pair: LensPairType.PAIR1
        }));

        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.ENHANCE,
            product: recommendedEnhance.product,
            pair: LensPairType.PAIR1
        }));

        // Note: Pair 2
        const complementaryEquipment = getComplementaryEquipmentValue();
        if(!complementaryEquipment){
            clearRecommendedPair2()
            return;
        }


        // COMPUTER
        if(complementaryEquipment === ComplementaryEquipmentType.COMPUTER){
            let recommendedCorrectPair2 = null;
            // CORRECT
            // Note: same correct for pair1 and pair2 if single vission 
            if(correctLensCategory === LensCategoryType.SINGLE_VISION) {
                recommendedCorrectPair2 = recommendedCorrect;
            } else {
                recommendedCorrectPair2 = getRecommendedProductPair2({
                    complementaryEquipment,
                    familyType: LensFamilyType.CORRECT,
                    products: temporaryProductList,
                    lensCategory: correctLensCategory
                })
            }
            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.CORRECT,
                product: recommendedCorrectPair2.product,
                subProduct: recommendedCorrectPair2.subProduct,
                pair: LensPairType.PAIR2
            }));

            //PROTECT
            const recommendeProtectPair2 = getRecommendedProductPair2({
                complementaryEquipment,
                familyType: LensFamilyType.PROTECT,
                products: temporaryProductList
            })
            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.PROTECT,
                product: recommendeProtectPair2.product,
                pair: LensPairType.PAIR2
            }));

            // ENHANCE
            const recommendedEnhancePair2 = getRecommendedProductPair2({
                complementaryEquipment,
                familyType: LensFamilyType.ENHANCE,
                products: temporaryProductList
            })
            
            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.ENHANCE,
                product: recommendedEnhancePair2.product,
                pair: LensPairType.PAIR2
            }));
            return;            
        }        

        //SUN and DRIVING
        if([ComplementaryEquipmentType.SUN, ComplementaryEquipmentType.DRIVING].includes(complementaryEquipment)){
            
            //CORRECT SAME AS PAIR1
            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.CORRECT,
                product: recommendedCorrect.product,
                pair: LensPairType.PAIR2
            }));

            // PROTECT
            const recommendedProtectPair2 = getRecommendedProductPair2({
                complementaryEquipment,
                familyType: LensFamilyType.PROTECT,
                products: temporaryProductList,
            })
            
            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.PROTECT,
                product: recommendedProtectPair2.product,
                pair: LensPairType.PAIR2
            }));

            // ENHANCE
            const recommendedEnhancePair2 = getRecommendedProductPair2({
                complementaryEquipment,
                familyType: LensFamilyType.ENHANCE,
                products: temporaryProductList,
            })

            dispatch(lensConsultationSlice.actions.selectProduct({
                lensFamily: LensFamilyType.ENHANCE,
                product: recommendedEnhancePair2.product,
                pair: LensPairType.PAIR2
            }));
            
        }
    }

    const clearRecommendedPair2 = () => {
        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.CORRECT,
            product: undefined,
            pair: LensPairType.PAIR2
        }));
        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.ENHANCE,
            product: undefined,
            pair: LensPairType.PAIR2
        }));
        dispatch(lensConsultationSlice.actions.selectProduct({
            lensFamily: LensFamilyType.PROTECT,
            product: undefined,
            pair: LensPairType.PAIR2
        }));
    }

    const saveRecommendLensThickness = (thickness: any) => {
        dispatch(lensThicknessSlice.actions.setLensThickness({
            pair1: thickness,
            pair2: thickness
        }));
        dispatch(userSlice.actions.saveLensThickness({
            pair1: thickness,
            pair2: thickness
        }));
    }

    const renderNextButtonLabel = () => {
        return (view === 'prescription') ? 'Next' : 'Save & Next';
    }

    const renderSideImage = () => {
        if (view === 'lens-proposal') {
            return <>
                <img src={clearLens} alt="" className={E('clear-lens')}/>
                {/* <PrescriptionLensOverlayAnimation/> */}
            </>;
        }
        // return `eye-reader-${tempPrescriptionData?.precision}`;
        return <div className={E('precision' ,[tempPrescriptionData?.precision === PrecisionType.AVA ? 'swap':''])}>
                    <Icon type="gif" extension="gif" name="eye-reader-ava"/>
                    <Icon type="image" name="eye-reader-standard"/>
                </div>
    }

    const onSelect = (id: string) => {
        //  alert(id);
    }

    return (
        
        <div className={B()}>
            <SideBoxBordered>
            
                <label className={E('title')}>
                    <Icon name="progress-circle"/>
                    Prescription
                </label>

                {renderSideImage()}

                <div className={E('buttons')}>
                    <Button icon="import" type="secondary"  click={() => setShowImportModal(true)}/>
                    <Button 
                        disabled={!isValidPrescription}
                        text={renderNextButtonLabel()} icon="right-arrow-circle-white" click={next}/>
                </div>

                <Modal show={showImportModal} width="43.2rem" height="25.8rem" onClose={() => setShowImportModal(false)}>
                    <div className={E('import-modal')}>
                        <h2>Import</h2>
                        
                        <div>
                            <Button 
                                
                                click={() => onSelect('qrcode')}
                                icon="qrcode"
                                text="QR Code" 
                                type="secondary"/> 
                            <Button 
                                click={() => onSelect('id')}
                                icon="stacked-group"
                                text="ID" 
                                type="secondary"/>
                        </div>

                    </div>
                </Modal>
            </SideBoxBordered>
        </div>
    )
}

export default PrescriptionSide;