import React, { useState } from 'react';
import Button from '../../../components/ui/Button';
import InputText from '../../../components/ui/InputText';
import iMeasurementDataThree from '../../../interface/measurement-three-data.inteface';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onSave: (data: iMeasurementDataThree) => void,
    data?: iMeasurementDataThree
}

const MeasureStepThree : React.FC<Props>  = ({
    onSave,
    data
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('measures-step');
    const [localData, setLocalData] = useState<iMeasurementDataThree>(data || {
        pantoscopicAngle: 0,
        wrapAngle: 0,
        vertexDistance: 0,
        readingDistance: 0    
    })

    // HOOKS

    // METHODS
    const onSaveHandler = () => {
        onSave(localData);
    }

    const inputChangeHandler = (e: any, key: string) => {
        setLocalData((prev: any) => {
            return {
                ...prev,
                [key]: e.target.value
            }
        })
    }

    return (
        <div className={B()}>
            <div className={E('form', 'three')}>

                <label>Pantoscopic Angle</label>
                <InputText type="light" value={localData?.pantoscopicAngle} onChange={(v) => inputChangeHandler(v, 'pantoscopicAngle')}/>

                <label>Wrap Angle</label>
                <InputText type="light" value={localData?.wrapAngle} onChange={(v) => inputChangeHandler(v, 'wrapAngle')}/>

                <label>Vertex distance</label>
                <InputText type="light" value={localData?.vertexDistance} onChange={(v) => inputChangeHandler(v, 'vertexDistance')}/>

                <label>Reading distance</label>
                <InputText type="light" value={localData?.readingDistance} onChange={(v) => inputChangeHandler(v, 'readingDistance')}/>

            </div>

            <Button text="Save & Next" icon="right-arrow-circle-white" className={E('button')} click={onSaveHandler}/>
        </div>
    )
}

export default MeasureStepThree;