import React, { useEffect, useState } from 'react';
import Button from '../../../components/ui/Button';
import InputText from '../../../components/ui/InputText';
import iMeasurementDataOne from '../../../interface/measurement-one-data.inteface';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onSave: (data: iMeasurementDataOne) => void,
    data?: iMeasurementDataOne
}

const MeasureStepOne : React.FC<Props>  = ({
    onSave,
    data
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('measures-step');
    const [localData, setLocalData] = useState<iMeasurementDataOne>(data || {
        price: 0,
        a:0,
        b:0,
        d: 0
    })

    // HOOKS

    // METHODS
    const onSaveHandler = () => {
        onSave(localData);
    }

    const inputChangeHandler = (e: any, key: string) => {
        setLocalData((prev: any) => {
            return {
                ...prev,
                [key]: e.target.value ? Number(e.target.value) : 0
            }
        })
    }

    return (
        <div className={B()}>

            <div className={E('form', 'one')}>
                <label>Price</label>
                <InputText placeholder="Enter the value" type="light" value={localData?.price} onChange={(v) => inputChangeHandler(v, 'price')}/>

                <label>A &#9645; B</label>
                <InputText placeholder="A" type="light" value={localData?.a} onChange={(v) => inputChangeHandler(v, 'a')}/>
                <InputText placeholder="B" type="light" value={localData?.b} onChange={(v) => inputChangeHandler(v, 'b')}/>

                <label>D</label>
                <InputText placeholder="Enter the value" type="light" value={localData?.d} onChange={(v) => inputChangeHandler(v, 'd')}/>
            </div>

            <Button text="Next" icon="right-arrow-circle-white" className={E('button')} click={onSaveHandler}/>
            
        </div>
    )
}

export default MeasureStepOne;